$primary-green: rgb(88, 137, 87);
$background-green: #cdd5cb; //that colour format consistency
$background-brown: #3d2b13;
$primary-brown: #4b3d1a;
$secondary-brown: #6b6149;
$button-shadow: 0px 0px 25px 0px rgba(61, 48, 25, 0.55);
$panel-shadow: 0px 0px 30px 0px rgba(61, 48, 25, 0.6);

#cart {
  margin-top: 2em;
  h1{
    margin: 0 0 1em 1em;
    color: $primary-brown;
    font-weight: 200;
  }
  .panel {
    .empty {
      width: 100%;
      text-align: center;
    }

    .table-container {
      margin: 0 2em 0 2em;
      table {
        width: 100%;
        td {
          color: $primary-brown;
          h2{
            margin: 0;
            font-weight: 200;
            padding-bottom: 0.25em;
          }
        }
        .name {
          padding-left: 0.5em;
        }
        .name, .quantity, .price, .total {
          color: $secondary-brown;
          font-weight: 200;
          div {
            display: inline;
          }
        }
        .quantity, .price, .total {
          text-align: right;
        }
      }
    }

    .cart-functions {
      margin: 2em 2em 0 0;
      text-align: right;
    }

  }/* panel ends */

  .fa {
    &:hover {
      color: $primary-green;
      cursor: pointer;
    }
  }
}/* CART ends */
